.container_saya {
  width: 100%;
  height: 60vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 900px) {
  .container_saya {
    width: 100vw;
    height: 100vh;
  }
}
.container_saya .saya {
  padding: 1rem;
  border: 1px solid #1c6dd0;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
@media screen and (min-width: 500px) {
  .container_saya .saya {
    padding: 1.4rem;
  }
}
@media screen and (min-width: 700px) {
  .container_saya .saya {
    padding: 1.8rem;
  }
}
@media screen and (min-width: 1500px) {
  .container_saya .saya {
    padding: 2.3rem;
  }
}
.container_saya .saya .saya2 {
  padding: 0.5rem;
  border: 1px solid #1c6dd0;
}
@media screen and (min-width: 500px) {
  .container_saya .saya .saya2 {
    padding: 1rem;
  }
}
@media screen and (min-width: 700px) {
  .container_saya .saya .saya2 {
    padding: 1.5rem;
  }
}
@media screen and (min-width: 1500px) {
  .container_saya .saya .saya2 {
    padding: 2rem;
  }
}
.container_saya .saya .saya2 h1 {
  display: flex;
  justify-content: left;
  flex-direction: row;
  font-size: 7vw;
  overflow: hidden;
  margin-bottom: 5vh;
}
@media screen and (min-width: 1000px) {
  .container_saya .saya .saya2 h1 {
    margin-bottom: 0vh;
  }
}